.main-news-section {
	padding: 15px 20px 15px 20px;
}

.main-news-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.main-news-section .section-title {
	margin-bottom: 15px;
	padding-left: 10px;
	border-left: 5px solid #0F5DFF;
}

.show-more {
	cursor: pointer;
	padding-bottom: 2px;
	border-bottom: 2px solid #0F5DFF;
}



