.header-container {
	display: -ms-flexbox;
	display: flex;
	border-bottom: 1px solid #ccc;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	padding: 15px 0;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.logo-container {
	display: -ms-flexbox;
	display: flex;
}

.logo {
	height: 50px;
	width: 50px;
}

.title {
	color: #000;
	position: relative;
	top: 12px;
}

.header-menu {
	padding-right: 30px;
}

.header-menu a {
	margin: 0 15px;
	padding-bottom: 5px;
	color: #000;
}

.active {
	border-bottom: 2px solid #0F5DFF;
}



