.prices-container {
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 30px;
	padding: 15px 0 0 0;
}

.price-item:first-child {
	margin: 0;
}

.price-item {
	-ms-flex-align: end;
	    align-items: flex-end;
	background: rgb(60, 60, 60);
	border-radius: 4px;
	padding: 15px 5px 15px 15px;
	margin: 10px 0;
	background: rgb(248, 248, 248);
	color: #000;
	list-style-type: none;
}

.price-container {
	display: -ms-flexbox;
	display: flex;
}

.symbol {
	font-size: 14px;
	color: rgb(160, 160, 160);
}

.price {
	font-size: 16px;
	display: inline-block;
	-ms-flex: 3 1;
	    flex: 3 1;
}

.dollar {
	font-size: 22px;
	position: relative;
	top: 2px;
}

.positive {
	color: rgb(110, 225, 167);
}

.negative {
	color: rgb(252, 168, 168);
}

.positive,
.negative {
	padding: 0 4px;
	-ms-flex: 1 1;
	    flex: 1 1;
	position: relative;
	top: 6px;
}



